* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container{
    max-width: 1000px;
    margin: 0 auto;

}
.header{
    margin-bottom: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.logo {
    display: flex;
    align-items: center;
    gap: 16px;
}
.logo img {
    width: 68px;
    height: 68px;
}
h1 {
    font-size: 42px;
    text-transform: uppercase;
    font-family: 'Cormorant Upright', sans-serif; 
    line-height: 1;
    margin-top: 6px;
    
}
.btn{
    border: none;
    color: inherit;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    line-height: 1;
    text-transform: uppercase;
    font-size: 17;
    padding: 16px 0 13px;
    background-image: linear-gradient(135deg, #3b82f6,#ef4444,#16a34a, #eab308);
    border-radius: 100px;
    cursor: pointer;
}

.btn-large{
    font-size: 30px;
    padding: 20px 32px 17px;

}
.fact-form {
background-color: #44403c;
margin-bottom: 40px;
padding: 16px 32px;
display: flex;
align-items: center;
gap: 16px;
border-radius: 16px;
}

.main{
    display: grid;
    grid-template-columns: 250px 1fr;
    gap: 48px;
    height: calc(100vh - 48px - 68px - 40px);

}
section{
    overflow: scroll;
    padding-bottom: 40px;
}
@media (max-width: 900px) {
    .main {
    grid-template-columns: 1fr;
    }
}
.category{
    margin-bottom: 16px;
}
.btn-all-categories{
    margin-bottom: 16px;
    width: 100%;
}
.btn-category{
    width: 100%;
    background-image: none;
}
.tag{
    text-transform: uppercase;
    font: 14px;
    padding-top: 3px;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 100px;
}
.vote-buttons{
    margin-left: auto;
   display: flex;
   gap: 8px;
   flex-shrink: 0;
}
.vote-buttons button {
    background-color: #78716e;
    border: none;
    font-size: 18px;
    padding: 6px 12px;
    border-radius: 100px;
    color: inherit;
    font-family: inherit;
    font-weight: 600;
    cursor: pointer;

}
.message {
text-align: center;
text-transform: uppercase;
font-size: 32px;
font-weight: 600;
margin-top: 24px;
}
.vote-buttons button:hover {
    background-color: #292524;
}
.vote-buttons button:disabled {
    background-color: #44403c;
}
.disputed {
    color: red;
    font-weight: 600;
    margin-right: 10px;
}
.fact {
    font-size: 20px;
    line-height: 1.4;
    font-family: sans-serif;
    background-color: #44403c;
    margin-bottom: 16px;
    padding: 16px 24px;  
    display: flex;
    align-items: center;
    gap: 24px;
    letter-spacing: -1px;
    border-radius: 16px;

}

@media (max-width: 1200px) {

.fact {
    flex-direction: column;
    gap: 12px;
    align-items: flex-end;
}

}


/* Layout */



ul {
    list-style: none;
    
}

body {
    background-color: #262626;
    color: #fafafa;
    padding: 48px 64px 0px;
}

.btn:hover{
    transform: scale(110%) rotate(-2deg);
}


.source:link,
.source:visited
{
    color: #a8a29e;
    text-decoration: none;
    margin-left: 12px;
    transition: 0.3s;
}
.source:hover,
.source:avtive
{
    color: #3b82f6;
}

.fact-form input,
.fact-form select {
    width: 220px;
    background-color: #78766c;
    border: none;
    border-radius: 100px;
    padding: 16px;
    font-size: 18px;
    color: inherit;
    font-family: inherit;
}
.fact-form input::placeholder {
    color: #a8a29e;
}
.fact-form span {
    font-weight: 600;
    font-size: 18px;
    margin-right: 18px;
}
.fact-form input:first-child{
flex-grow: 1;
}
.hidden {
    display: none !important;

}

@media (max-width: 1000px) {
    body {
        /* padding: 32px 48px; */
    }
    .fact-form {
        flex-direction: column;
        align-items: stretch;
    }
    
    .fact-form input,
    .fact-form select{
        width: auto;
    }
    
    
    }
    @media (max-width: 900px) {
        .main {
          grid-template-columns: 1fr;
          height: auto;
        }
      }
       
      @media (max-width: 600px) {
        #root {
          padding: 24px;
        }
       
        .header {
          flex-direction: column;
          gap: 16px;
        }
       
        .fact-form {
          padding: 16px;
        }
       
        .fact {
          padding: 12px 16px;
        }
      }
